
import React from "react";
import { useLocation, Route, Switch, Redirect } from "react-router-dom";
import { useSelector } from "react-redux";
// reactstrap components
import { Container } from "reactstrap";
// core components
import AdminNavbar from "../components/Navbars/AdminNavbar.js";
import AdminFooter from "../components/Footers/AdminFooter.js";
import Sidebar from "../components/Sidebar/Sidebar.js";


import routes from "../routes.js";
import workforceRoutes from '../routes/workforceRoute.js'
import CustomNavbar from "../components/Navbar/footerNavbar.js";
import logo from "../assets/img/brand/logo.png"

const Admin = (props) => {
  
  const mainContent = React.useRef(null);
  const location = useLocation();

  var isLoading = useSelector((state) => state.loading)

  React.useEffect(() => {
    document.documentElement.scrollTop = 0;
    document.scrollingElement.scrollTop = 0;
    mainContent.current.scrollTop = 0;
  }, [location]);

  const getRoutes = (routes) => {
    return routes.map((prop, key) => {
      if (prop.layout === "/app") {
        return (
          <Route
            path={prop.layout + prop.path}
            component={prop.component}
            key={key}
          />
        );
      } else {
        return null;
      }
    });
  };

  const getBrandText = (path) => {
    for (let i = 0; i < routes.length; i++) {
      if (
        props.location.pathname.indexOf(routes[i].layout + routes[i].path) !==
        -1
      ) {
        
        return routes[i].name;
      }
    }
    return "";
  };

  return (
    <>
    {isLoading && (
          <div className="loading">
            <img src={logo}/>
          </div>
      )}
      <Sidebar
        {...props}
        routes={routes}
        logo={{
          innerLink: "/app",
          imgSrc: require("../assets/img/brand/brandlogo.png").default,
          imgAlt: "...",
        }}
      />
      <div className="main-content" ref={mainContent}>
        <AdminNavbar
          {...props}
          brandText={getBrandText(props.location.pathname)}
        />
        <Switch>
          {getRoutes(workforceRoutes)}
          {/* <Redirect from="*" to="/app" /> */}
        </Switch>
        <Container fluid>
          <AdminFooter />
        </Container>
        
      </div>
      <CustomNavbar/>
    </>
  );
};

export default Admin;
